<template>
  <div class="content">
    <router-view />
    <div class="open" @click="open">展开>></div>
    <div :class="{ 'bottom-nav': true, close: showSlider }">
      <div class="close_btn" @click="close"><<收起</div>
      <div
        @click="goPage(item.URL, index)"
        :class="{ item: true, active: current == index }"
        v-for="(item, index) in menu"
        :key="index"
      >
        {{ item.name }}
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "mainIndex",
  data() {
    return {
      showSlider: false,
      current: localStorage.getItem("currentIndex")
        ? localStorage.getItem("currentIndex")
        : 0,
      menu: [
        // {
        //   name: "主屏",
        //   URL: "/bigscreen2",
        // },
        {
          name: "光伏管理系统",
          URL: "/pv",
        },
        {
          name: "储能管理系统",
          URL: "/energyStorage",
        },
        {
          name: "充电管理系统",
          URL: "/charge",
        },
        {
          name: "配电管理系统",
          URL: "/distribution",
        },
        {
          name: "重点用能设备",
          URL: "/equipment",
        },
         {
          name: "能源绩效考核",
          URL: "/KPI",
        },
      ],
    };
  },
  methods: {
    goPage(url, index) {
      this.$router.replace(url);
      this.current = index;
      localStorage.setItem("currentIndex", index);
      this.showSlider = false;
    },
    close() {
      this.showSlider = false;
    },
    open() {
      this.showSlider = true;
    },
  },
  created() {
    document.title = this.$route.meta.title;
  },
  beforeRouteUpdate(to, from, next) {
    document.title = to.meta.title;
    next();
  },
};
</script>

<style lang="less">
.content {
  width: 100%;
  height: 100%;
}
.close_btn {
  margin-top: 30px;
  cursor: pointer;
}

.open {
  position: fixed;
  z-index: 1000;
  top: 100px;
  left: 0px;
  width: 50px;
  height: 30px;
  line-height: 30px;
  background: #fff;
  font-size: 12px;
  text-align: center;
  background: rgba(255, 255, 255, 0.5);
}

.bottom-nav {
  width: 120px;
  height: 100%;
  background-color: #ffffff;

  position: fixed;
  z-index: 1001;
  top: 0;
  bottom: 0px;
  left: -120px;
  padding: 0px 10px;
  box-sizing: border-box;
  .item {
    margin-top: 30px;
    cursor: pointer;
  }
  .active {
    color: red;
  }
}
.bottom-nav.close {
  animation-name: example;
  animation-duration: 0.4s;
  left: 0;
}

@keyframes example {
  from {
    left: -120px;
  }
  to {
    left: 0px;
  }
}
</style>
